.testHome {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.test-title {
    color: var(--c-text-primary);
    font-size: 2.4rem;
    font-weight: 500;
    text-align: center;
    margin: 20px 0px 0px 0px;
}

.test-description {
    color: var(--c-text-primary);
    font-size: 1.2rem;
    font-weight: 600;
    text-align: center;
    width: 800px;
}

.list-test {
    display: flex; 
    justify-content: center;
    flex-wrap: wrap;
}

.test-banner-img {
    width: 240px;
    height: 200px;
    margin-top: -20px;
}

.test-banner {
    background-color: var(--background-banner);
    max-width: 200px;
    max-height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px;
    padding: 20px;
    flex: 1;
}

.test-banner-title {
    font-size: 1rem;
    width: 180px;
    height: 80px;
    text-align: center;
}

.test-banner-button {
    background: var(--c-text-secundary);
    color: var(--c-text-black);
    text-decoration: none;
    width: 180px;
    height: 60px;
    font-size: 1.2rem;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    margin-top: 10px;
}

.test-banner-button:hover {
    background-color: var(--c-text-secundary-hover);
}
